import React from 'react';
import {HashLink as Link} from 'react-router-hash-link'
// import SegmentShow from './SegmentShow'


class Segment extends React.Component{

    render(){

        const {key, toggle, segmentImage, title, countryLink} = this.props

        return(
            <div id="intl" key={key} className="container" onClick={toggle}>
                    <Link to={countryLink}>
                        <div className="intl-pic-box">
                            <img src={segmentImage} alt="country"/>
                        </div>
                        <div className="row">
                            <h2>{title}</h2><ion-icon name="arrow-round-forward"></ion-icon>
                        </div>
                    </Link>
            </div>
        );
    }
}

export default Segment