import React, {Component} from 'react';
import {HashLink as Link} from 'react-router-hash-link'
import logo from '../../imgs/logo-white.png'
import './navigation.css'


class Navigation extends Component{

    state = {
        scrolled: false
    }   

    componentDidMount(){
        window.addEventListener('scroll', () => {
           const isTop = window.scrollY < 560;
           if (isTop !== true) {
               this.setState({scrolled: true})
           } else {
               this.setState({scrolled: false})
           }
        })
    }


    render(){

        return(
            <React.Fragment>
                <nav id="main-nav" className={this.state.scrolled ? 'scrolled' : ''}>
                    <Link to="/#header"><img src={logo} className="official-logo" alt="logo" /></Link>
                    <ul id="navigation-links">
                        <li><Link className="nav-links" to="/#header"><i className="home icon"></i></Link></li>
                        <li><Link className="nav-links" to="/about#header">Quem Somos</Link></li>
                        <li><Link className="nav-links" to="/atividade#header">Atuação</Link></li>
                        {/* <li><Link className="nav-links" to="/especializacao#header">Especialização</Link></li> */}
                        <li><Link className="nav-links" to="/internacional#header">Internacional</Link></li>
                        {/* <li><Link className="nav-links" to="/noticias#header">Notícias</Link></li>
                        <li><Link className="nav-links" to="/contratacao#header">Contratação</Link></li> */}
                        <li><Link className="nav-links" to="/#clientes">Contacto</Link></li>
                    </ul>
                </nav> 
             </React.Fragment>
        )
    }
} 


export default Navigation;
