import React from 'react'
import Navigation from './Navigation'
import MobileNav from '../MobileNav/MobileNav'
import MobileNavButton from '../MobileNavButton/MobileNavButton'
import './header.css'

class Header extends React.Component{

    state= {
        mobileNavOpen: false,
    }

    mobileNavTogglerHandler = () => {
        this.setState((prevState) => {
            return {mobileNavOpen: !prevState.mobileNavOpen};
        });
    }

    linkOnClikHandler = () => {
        this.setState( {mobileNavOpen: false });
    }

    render(){
        const { imageBackground, heading, subheading } = this.props;

        return(
            <React.Fragment>
            <section id="header" style={{   backgroundImage:`linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.5)), url(${imageBackground})`,
                                            backgroundSize: 'cover',
                                            backgroundRepeat: 'no-repeat',
                                            backgroundPosition: 'fixed'}}>
                <Navigation />
                <MobileNavButton click={this.mobileNavTogglerHandler}/>
                <MobileNav show={this.state.mobileNavOpen} linkOnClik={this.linkOnClikHandler}/>
                <h1>{heading}</h1>
                <h2>{subheading}</h2>
            </section>
            {/* <div className="container-share">
                <div className="row">
                    <span><i className="share alternate icon"></i>Partilhar</span>
                </div>
            </div> */}
            </React.Fragment>
        );
    }
}

export default Header

