import React from 'react'

class AreaBox extends React.Component{
    render(){

        const {image, areaTitle, description} = this.props
        
        return(
            <div id="areabox">
                <div className="box-image" style={{ backgroundColor: `linearGradient(rgba(0,0,0,0.3), rgba(0,0,0,0.4))`}}>
                    <img src={image} alt="área de atividade"/>
                </div>
                <div className="row">
                        <h4>{areaTitle}</h4><ion-icon name="arrow-forward"></ion-icon>
                </div>
                <p>{description}</p>
            </div>
        );
    }
}

export default AreaBox