import React from 'react'
import { HashLink as Link } from 'react-router-hash-link'
import './mobilenav.css'

class MobileNav extends React.Component{
    render(){

        let mobileNavClasses = ['mobile-nav'];
        if (this.props.show){
            mobileNavClasses = 'mobile-nav slide';
        }

        let mobileLinks = document.getElementsByClassName('mobile-links');
        for (let i=0; i<mobileLinks.length; i++){
            mobileLinks[i].setAttribute("onClick", `${this.props.linkOnClik}`)
        }

        return(
            <nav className={mobileNavClasses}>
                <ul>
                    <li><Link className="mobile-links" to="/">Início</Link></li>
                    <li><Link className="mobile-links" to="/atividade#header">Áreas de Atuação</Link></li>
                    <li><Link className="mobile-links" to="/internacional#header">Internacional</Link></li>
                    <li><Link className="mobile-links" to="/contacto#header">Contacto</Link></li>
                </ul>
            </nav>
        );
    }
}
export default MobileNav