import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import { withCookies } from 'react-cookie';
// import './App.css';
// import './vendors/normalize.min.css'
// import Navigation from './components/Header/Navigation'
import Footer from './components/Footer/Footer'
import Inicio from './pages/inicio/Inicio'
import About from './pages/about/About'
import Atividade from './pages/atividade/Atividade'
// import Especializacao from '../pages/pages/especializacao/Especializacao'
import Noticias from './pages/noticias/Noticias'
import Internacional from './pages/internacional/Internacional'
import Contratacao from './pages/contratacao/Contratacao'
import Contacto from './pages/contacto/Contacto'
import Informacao from './pages/informacao/Informacao'
// import Copyright from '../pages/pages/copyright/Copyright'
import Termos from './pages/termos/Termos'
import Privacidade from './pages/privacidade/Privacidade'
import Acessibilidade from './pages/acessibilidade/Acessibilidade'
import PageNotFound from './pages/404/PageNotFound'
import Civil from './pages/atividade/area/civil/Civil'
import Comercial from './pages/atividade/area/comercial/Comercial'
import Contencioso from './pages/atividade/area/contencioso/Contencioso'
import Familia from './pages/atividade/area/familia/Familia'
import Laboral from './pages/atividade/area/laboral/Laboral'
import Imob from './pages/atividade/area/imob/Imob'
import Penal from './pages/atividade/area/penal/Penal'
import Sociedades from './pages/atividade/area/sociedades/Sociedades'
import Migracao from './pages/atividade/area/migracao/Migracao'
import Acidentes from './pages/atividade/area/acidentes/Acidentes'
import Danos from './pages/atividade/area/danos/Danos'
import Moz from './pages/internacional/Moz'
import Pt from './pages/internacional/Pt'
import Sa from './pages/internacional/Sa'

class App extends Component {

  render() {

    return (
        <Router>
          <div className="App">
            <div className="container">
            <Switch>
              <Route exact path="/" render={() => (<Inicio cookies={this.props.cookies}/>)}/>
              <Route exact path="/about" component={About} />
              <Route exact path="/atividade" component={Atividade}/>
              {/* <Route exact path="/especializacao" component={Especializacao}/> */}
              <Route exact path="/contratacao" component={Contratacao}/>
              <Route exact path="/internacional" component={Internacional}/>
              <Route exact path="/noticias" component={Noticias}/>
              <Route exact path="/contacto" component={Contacto}/>
              {/* <Route exact path="/copyright" component={Copyright}/> */}
              <Route exact path="/informacao" component={Informacao}/>
              <Route exact path="/termos" component={Termos}/>
              <Route exact path="/privacidade" component={Privacidade}/>
              <Route exact path="/acessibilidade" component={Acessibilidade}/>
              <Route exact path="/404" component={PageNotFound}/>
              <Route exact path="/area/civil" component={Civil}/>
              <Route exact path="/area/comercial" component={Comercial}/>
              <Route exact path="/area/contencioso" component={Contencioso}/>
              <Route exact path="/area/familia" component={Familia}/>
              <Route exact path="/area/laboral" component={Laboral}/>
              <Route exact path="/area/imob" component={Imob}/>
              <Route exact path="/area/penal" component={Penal}/>
              <Route exact path="/area/sociedades" component={Sociedades}/>
              <Route exact path="/area/migracao" component={Migracao}/>
              <Route exact path="/area/acidentes" component={Acidentes}/>
              <Route exact path="/area/danos" component={Danos}/>
              <Route exact path="/internacional/mocambique" component={Moz}/>
              <Route exact path="/internacional/portugal" component={Pt}/>
              <Route exact path="/internacional/southafrica" component={Sa}/>
            </Switch>
            <Footer />
            </div>
          </div>
        </Router>
    );
  }
}

export default withCookies(App);